import { Component, OnInit, OnDestroy, ElementRef, QueryList, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators, ValidatorFn } from '@angular/forms';
import { Observable, Subscription } from 'rxjs';
import { AddressInfo,
  Agency,
  AgencyProfile,
  Applicant,
  CreditCardInfo,
  EnrollmentType,
  PersonInfo,
  PaymentInfo,
  RefData,
  TransactionInfo,
  ValidationError
} from '../../models';
import { DataService } from '../../services/data.service';
import { StepperService } from '../stepper/stepper.service';
import * as moment from 'moment';

@Component({
  selector: 'applicant-information',
  templateUrl: './applicant-information.component.html',
  styleUrls: ['./applicant-information.component.css']
})
export class ApplicantInformationComponent implements OnInit {

  @ViewChild('ssn') social: ElementRef;
  @ViewChild('vssn') vsocial: ElementRef;
  @ViewChild('email') email: ElementRef;
  @ViewChild('vemail') vemail: ElementRef;

  applicant: Applicant;
  privacyAgency: string;
  privacyAgencyId: string;
  enrollmentForm: FormGroup;
  enrollmentFormSubscription: Subscription;
  enrollmentFormOptionDefault: RefData = { value: null, name: 'SELECT' };

  socialSecurityNumberMatch: boolean;
  emailAddressesMatch: boolean;
  validEnrollmentForm: boolean;

  requiredFields: string[] = [];
  invalidFields: ValidationError[] = [];

  constructor(public dataService: DataService, private stepperService: StepperService) { }

  ngOnInit() {
    this.getPrivacyAgency();
    this.setupForm();
    this.applicant = {
      id: null,
      confirmation: null,
      status: null,
      transaction: null,
      person: null,
      address: null,
      payment: null
    }
  }

  ngOnDestroy() {
    this.enrollmentFormSubscription.unsubscribe();
  }

  /* For Demo Only populates user from assets/files/applicant.json file*/
  demoApplicantData() {
    this.dataService.getTestApplicant()
    .subscribe(response=> {
      let res = response.json();
      /* A few fields need to be manually populated as they the data is created dynamically */
      res.applicant.transaction.reason = this.dataService.reasonOptions[0].value;
      res.applicant.person.dateOfBirth = moment(parseInt(res.applicant.person.dateOfBirth)).toDate();
      console.log(moment(10011978).toDate())
      this.enrollmentForm.patchValue(res.applicant);
      this.vemail.nativeElement.value = res.applicant.address.email;
      this.validateForm();
    });
  }

  /* Get Abstract Control from Form Group */
  getControl(control: string, parent?: string): any {
    if(parent) {
      return this.enrollmentForm.get(parent).get(control);
    } else {
      return this.enrollmentForm.get(control);
    }
  }

  /* Check if form control/field failed vailidation */
  getError(control: string): boolean {
    return this.invalidFields.findIndex(f=> f.field === control) > -1;
  }

  /* Return error message for field with failed validation */
  getErrorMessage(control: string): string {
    let error: ValidationError = this.invalidFields.find(f=> f.field === control);
    return !error.message ? 'Required field' : error.message;
  }

  /* Return the associated label text for form field */
  getLabelText(control: string): string {
    return this.dataService.formMap.find(m=> m.field === control);
  }

  /* Get selected agency from privacy form in localstorage */
  getPrivacyAgency() {
    let privacy = JSON.parse(localStorage.getItem('privacy'));
    if(privacy) {
      this.privacyAgency = privacy.agency.name;
      this.privacyAgencyId = privacy.agency.id;
    }
  }

  /* Creates an array of values from keys in an object */
  getValuesArrayFromObject(values: any): any[] {
    let objArray: any[] = [];
    Object.keys(values).forEach(v=> objArray.push(values[v]));
    return objArray;
  }

  /* Call stepper service to go back a step */
  goToPreviousStep() {
    this.stepperService.goToPreviousStep();
  }

  /* Test if value is an object */
  isObject(value: any): boolean {
    return value instanceof Object;
  }

  /* Reset form and invalid field array */
  resetEnrollmentForm() {
    this.invalidFields = [];
    this.enrollmentForm.reset();
  }

  /* Create Enrollment Form Group */
  setupForm() {
    this.enrollmentForm = new FormGroup({
      transaction: new FormGroup({
        reviewingAgencyId: new FormControl(null, Validators.required),
      	reason: new FormControl(null, Validators.required),
      	requestingAgengyId: new FormControl(),
      	appliedPosition: new FormControl(),
      	payment: new FormControl(),
      	fingerPrintCardUser: new FormControl()
      }),
      person: new FormGroup({
        lastName: new FormControl(null, Validators.required),
      	firstName: new FormControl(null, Validators.required),
      	middleName: new FormControl(),
      	suffix: new FormControl(),
      	ssn: new FormControl(null, [Validators.minLength(10), Validators.maxLength(10), Validators.pattern('^[0-9]*$')]),
      	dateOfBirth: new FormControl(null, Validators.required),
      	weight: new FormControl(null, [Validators.required, Validators.pattern('^[0-9]*$')]),
      	gender: new FormControl(null, Validators.required),
      	race: new FormControl(null, Validators.required),
      	eyeColor: new FormControl(null, Validators.required),
      	hairColor: new FormControl(null, Validators.required),
      	height: new FormControl(null, Validators.required),
      	placeOfBirth: new FormControl(null, Validators.required),
      	countryOfCitizenship: new FormControl(),
      	stateDriversLicensce: new FormControl(),
      	dirversLicenseNumber: new FormControl(),
      }),
      address: new FormGroup({
        address1:	new FormControl(null, Validators.required),
        address2:	new FormControl(),
        city:	new FormControl(null, Validators.required),
        apt: new FormControl(),
        state: new FormControl(null, Validators.required),
        zip: new FormControl(null, [Validators.required, Validators.minLength(5), Validators.maxLength(5), Validators.pattern('^[0-9]*$')]),
        phone: new FormControl(null, [Validators.minLength(10), Validators.maxLength(10), Validators.pattern('^[0-9]*$')]),
        email: new FormControl(null, [Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$'), Validators.required]),
      })
    });

    this.requiredFields = [
      'reviewingAgencyId',
      'reason',
      'lastName',
      'firstName',
      'dateOfBirth',
      'weight',
      'gender',
      'race',
      'eyeColor',
      'hairColor',
      'height',
      'placeOfBirth',
      'phone',
      'email',
      'address1',
      'city',
      'state',
      'zip'
    ];

    const reviewingAgencyId = this.getControl('reviewingAgencyId', 'transaction');
    reviewingAgencyId.patchValue(this.privacyAgencyId);
    this.enrollmentFormSubscription = this.enrollmentForm.valueChanges.subscribe(value=> this.validateForm());
  }

  /* Submit form */
  submitEnrollmentForm() {
    /* Form Validated */
    this.applicant = Object.assign(this.applicant, this.enrollmentForm.value);
    localStorage.setItem('applicant', JSON.stringify(this.applicant));
    console.log('Form validated go to next step')
    this.stepperService.completeStep();
  }

  /* Test form validation and set button toggle variable */
  validateForm() {
    if(this.enrollmentForm.valid && this.validateInputs()) {
      this.validEnrollmentForm = true;
    } else {
      this.validEnrollmentForm = false;
    }
    console.log(this.enrollmentForm.value);
    console.log(this.validEnrollmentForm);
  }

  /* Validate form inputs*/
  validateInputs(): boolean {
    this.invalidFields = [];
    // this.validateRequiredFields(this.enrollmentForm.value);
    this.validateDateOfBirth();
    this.validateEmailAddress();
    this.validateSocialSocialSecurityNumber();
    console.log(this.invalidFields)
    return this.invalidFields.length === 0;
  }

  /* Check if required fields have values */
  validateRequiredFields(value: any) {
    Object.keys(value).forEach(k=> {
      if(value[k] instanceof Object) {
        this.validateRequiredFields(value[k]);
      } else {
        if(this.requiredFields.indexOf(k) > -1 && !value[k]) this.invalidFields.push({ field: k, required: true});
      }
    });
  }

  /* Test if dob is valid date */
  validateDateOfBirth() {
    /* Any age verification */
    if(this.invalidFields.findIndex(f=> f.field === 'dateOfBirth') === -1) {
      let d = moment(this.getControl('dateOfBirth', 'person').value);
      if(!d.isValid()) this.invalidFields.push({ field: 'dateOfBirth', message: 'Invalid Date of Birth'});
    }
  }

  /* Test input values match */
  verifyInputsMatch(element: ElementRef, verifyElement: ElementRef): boolean {
    return element.nativeElement.value === verifyElement.nativeElement.value;
  }

  /* Test email inputs for null/matching values */
  validateEmailAddress() {
    if(this.invalidFields.findIndex(f=> f.field === 'email') === -1) {
      if(this.email.nativeElement.value && this.vemail.nativeElement.value) {
        if(this.email && this.vemail) {
          this.emailAddressesMatch = this.verifyInputsMatch(this.email, this.vemail);
        } else {
          this.emailAddressesMatch = false;
        }
        if(!this.emailAddressesMatch) this.invalidFields.push({ field: 'email', message: 'Email addresses do not match'});
      } else {
        this.invalidFields.push({ field: 'email', message: 'Email confirmation required'});
      }
    }
  }

  /* Test ssn matching values if exists */
  validateSocialSocialSecurityNumber() {
    if(this.social && this.vsocial) {
      this.socialSecurityNumberMatch = this.verifyInputsMatch(this.social, this.vsocial);
    } else {
      this.socialSecurityNumberMatch = false;
    }
    if(this.socialSecurityNumberMatch && isNaN(this.social.nativeElement.value)) {
      this.invalidFields.push({ field: 'ssn', message: 'SSN must contain numbers only'});
    } else if(!this.socialSecurityNumberMatch) {
      this.invalidFields.push({ field: 'ssn', message: 'SSN does not match'});
    }
  }

}
