import { Component, OnInit, OnDestroy, ElementRef, QueryList, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators, ValidatorFn } from '@angular/forms';
import { Observable, Subscription } from 'rxjs';
import { AddressInfo,
  Agency,
  AgencyProfile,
  Applicant,
  CreditCardInfo,
  EnrollmentType,
  PersonInfo,
  PaymentInfo,
  RefData,
  TransactionInfo,
  ValidationError
} from '../../models';
import { DataService } from '../../services/data.service';
import { StepperService } from '../stepper/stepper.service';
import * as moment from 'moment';

@Component({
  selector: 'payment-information',
  templateUrl: './payment-information.component.html',
  styleUrls: ['./payment-information.component.css']
})
export class PaymentInformationComponent implements OnInit {

  applicant: Applicant;
  paymentInfo: PaymentInfo;
  creditCardInfo: CreditCardInfo;
  paymentAccepted: boolean;
  privacyAgency: string;
  privacyAgencyId: string;
  enrollmentForm: FormGroup;
  enrollmentFormSubscription: Subscription;
  enrollmentFormOptionDefault: RefData = { value: null, name: 'SELECT' };

  price: number;
  brand: string;
  creditNumberValid: boolean;
  discountApplied: boolean;

  validEnrollmentForm: boolean;

  requiredFields: string[] = [];
  invalidFields: ValidationError[] = [];

  constructor(public dataService: DataService, private stepperService: StepperService) { }

  ngOnInit() {

    this.price = 36.25; // We'll get this value from a service later
    this.creditNumberValid = false;

    this.getPrivacyAgency();
    this.setupForm();
    this.applicant = {
      id: null,
      confirmation: null,
      status: null,
      transaction: null,
      person: null,
      address: null,
    }
    this.creditCardInfo = {
      cardType: null,
      cardHolder: null,
      cardNumber: null,
      csv: null,
      expirationDate: null,
    }
  }

  ngOnDestroy() {
    this.enrollmentFormSubscription.unsubscribe();
  }

  /* For Demo Only populates user from assets/files/applicant.json file*/
  demoApplicantData() {
    this.dataService.getTestApplicant()
    .subscribe(response=> {
      let res = response.json();
    });
  }

  /* Get Abstract Control from Form Group */
  getControl(control: string, parent?: string): any {
    if(parent) {
      return this.enrollmentForm.get(parent).get(control);
    } else {
      return this.enrollmentForm.get(control);
    }
  }

  /* Check if form control/field failed vailidation */
  getError(control: string): boolean {
    return this.invalidFields.findIndex(f=> f.field === control) > -1;
  }

  /* Return error message for field with failed validation */
  getErrorMessage(control: string): string {
    let error: ValidationError = this.invalidFields.find(f=> f.field === control);
    return !error.message ? 'Required field' : error.message;
  }

  /* Return the associated label text for form field */
  getLabelText(control: string): string {
    return this.dataService.formMap.find(m=> m.field === control);
  }

  /* Get selected agency from privacy form in localstorage */
  getPrivacyAgency() {
    let privacy = JSON.parse(localStorage.getItem('privacy'));
    if(privacy) {
      this.privacyAgency = privacy.agency.name;
      this.privacyAgencyId = privacy.agency.id;
    }
  }

  /* Creates an array of values from keys in an object */
  getValuesArrayFromObject(values: any): any[] {
    let objArray: any[] = [];
    Object.keys(values).forEach(v=> objArray.push(values[v]));
    return objArray;
  }

  /* Call stepper service to go back a step */
  goToPreviousStep() {
    this.stepperService.goToPreviousStep();
  }

  /* Test if value is an object */
  isObject(value: any): boolean {
    return value instanceof Object;
  }

  /* Reset form and invalid field array */
  resetEnrollmentForm() {
    this.invalidFields = [];
    this.enrollmentForm.reset();
  }

  // We should update this with server communication
  checkCode(code){
    if(code == 'ABCD') {
      this.price = 0.00
      this.discountApplied = true
    } else {
      this.price = 36.25
      this.discountApplied = false
    }
  }

  luhn_validate(imei){
      return !/^\d+$/.test(imei) || (imei.split('').reduce(function(sum, d, n){
              return n===(imei.length-1)
                     ? 0
                     : sum + parseInt((n%2)? d: [0,2,4,6,8,1,3,5,7,9][d]);
          }, 0)) % 10 == 0;
  };

  cc_brand_id(cur_val) {
    // the regular expressions check for possible matches as you type, hence the OR operators based on the number of chars
    // regexp string length {0} provided for soonest detection of beginning of the card numbers this way it could be used for BIN CODE detection also

    //JCB
    let jcb_regex = new RegExp('^(?:2131|1800|35)[0-9]{0,}$'); //2131, 1800, 35 (3528-3589)
    // American Express
    let amex_regex = new RegExp('^3[47][0-9]{0,}$'); //34, 37
    // Diners Club
    let diners_regex = new RegExp('^3(?:0[0-59]{1}|[689])[0-9]{0,}$'); //300-305, 309, 36, 38-39
    // Visa
    let visa_regex = new RegExp('^4[0-9]{0,}$'); //4
    // MasterCard
    let mastercard_regex = new RegExp('^(5[1-5]|222[1-9]|22[3-9]|2[3-6]|27[01]|2720)[0-9]{0,}$'); //2221-2720, 51-55
    let maestro_regex = new RegExp('^(5[06789]|6)[0-9]{0,}$'); //always growing in the range: 60-69, started with / not something else, but starting 5 must be encoded as mastercard anyway
    //Discover
    let discover_regex = new RegExp('^(6011|65|64[4-9]|62212[6-9]|6221[3-9]|622[2-8]|6229[01]|62292[0-5])[0-9]{0,}$');
    ////6011, 622126-622925, 644-649, 65


    // get rid of anything but numbers
    cur_val = cur_val.replace(/\D/g, '');

    // checks per each, as their could be multiple hits
    var sel_brand = null;
    if (cur_val.match(jcb_regex)) {
      sel_brand = "jcb";
    } else if (cur_val.match(amex_regex)) {
      sel_brand = "amex";
    } else if (cur_val.match(diners_regex)) {
      sel_brand = "dinerclub";
    } else if (cur_val.match(visa_regex)) {
      sel_brand = "visa";
    } else if (cur_val.match(mastercard_regex)) {
      sel_brand = "master";
    } else if (cur_val.match(discover_regex)) {
      sel_brand = "discover";
    } else if (cur_val.match(maestro_regex)) {
      if (cur_val[0] == '5') { //started 5 must be mastercard
        sel_brand = "master"; //mastercard
      } else {
        sel_brand = "master"; //maestro (mastercard) is all 60-69 which is not something else, thats why this condition in the end
      }
    }

    return sel_brand;
  }

  valid_credit_card(value) {
    this.brand = this.cc_brand_id(value)
    // accept only digits, dashes or spaces
  	if (/[^0-9-\s]+/.test(value)) {
      this.creditNumberValid = false
      return false;
    }

  	// The Luhn Algorithm. It's so pretty.
  	var nCheck = 0, nDigit = 0, bEven = false;
  	value = value.replace(/\D/g, "");

  	for (var n = value.length - 1; n >= 0; n--) {
  		var cDigit = value.charAt(n),
  			  nDigit = parseInt(cDigit, 10);

  		if (bEven) {
  			if ((nDigit *= 2) > 9) nDigit -= 9;
  		}

  		nCheck += nDigit;
  		bEven = !bEven;
  	}

    console.log(value.length)
    if (value.length > 0) {
      this.creditNumberValid = (nCheck % 10) == 0 // Change the model
    } else {
      this.creditNumberValid = false // Change the model
    }
  	return (nCheck % 10) == 0;
  }

  /* Create Enrollment Form Group */
  setupForm() {
    this.enrollmentForm = new FormGroup({
      payment: new FormGroup({
        reviewingAgencyId: new FormControl(null, Validators.required),
        paymentType: new FormControl(null, Validators.required),
        discountCode: new FormControl(null),
        creditName: new FormControl(null, Validators.required),
        creditNumber: new FormControl(null, [
          Validators.pattern('^[0-9]*$')]),
        creditMonth: new FormControl(null, [
          Validators.minLength(2),
          Validators.maxLength(2),
          Validators.min(1),
          Validators.max(12),
          Validators.pattern('^[0-9]*$')]),
        creditYear: new FormControl(null, [
          Validators.minLength(4),
          Validators.maxLength(4),
          Validators.min(2018),
          Validators.max(2100),
          Validators.pattern('^[0-9]*$')]),
        creditCSV: new FormControl(null, [Validators.pattern('^[0-9]*$')]),
      })
    });

    this.requiredFields = [
      'reviewingAgencyId',
      'paymentType',
      'creditName',
      'creditNumber',
      'creditMonth',
      'creditYear',
      'creditCSV',
    ];

    const reviewingAgencyId = this.getControl('reviewingAgencyId', 'payment');
    reviewingAgencyId.patchValue(this.privacyAgencyId);
    const paymentType = this.getControl('paymentType', 'payment');
    paymentType.patchValue('Credit');
    this.enrollmentFormSubscription = this.enrollmentForm.valueChanges.subscribe(value=> this.validateForm(value));

    const discountCode = this.getControl('discountCode', 'payment');
    discountCode.valueChanges.subscribe(
      changes => this.checkCode(changes)
    );
    const creditNumber = this.getControl('creditNumber', 'payment');
    creditNumber.valueChanges.subscribe(
      changes => console.log('creditNumber has changed:', this.valid_credit_card(changes))
    );

  }

  /* Submit form */
  submitEnrollmentForm() {
    /* Form Validated */
    this.applicant = Object.assign(this.applicant, this.enrollmentForm.value);
    localStorage.setItem('applicant', JSON.stringify(this.applicant));
    console.log('Form validated go to next step')
  }

  /* Test form validation and set button toggle variable */
  validateForm(formValue: any) {
    if(this.enrollmentForm.valid && this.validateInputs()) {
      this.validEnrollmentForm = true;
    } else {
      this.validEnrollmentForm = false;
    }
    console.log(formValue);
    console.log(this.validEnrollmentForm);
  }

  /* Validate form inputs*/
  validateInputs(): boolean {
    this.invalidFields = [];
    // this.validateRequiredFields(this.enrollmentForm.value);
    console.log(this.invalidFields)
    return this.invalidFields.length === 0;
  }

  /* Check if required fields have values */
  validateRequiredFields(value: any) {
    Object.keys(value).forEach(k=> {
      if(value[k] instanceof Object) {
        this.validateRequiredFields(value[k]);
      } else {
        if(this.requiredFields.indexOf(k) > -1 && !value[k]) this.invalidFields.push({ field: k, required: true});
      }
    });
  }

  /* Test input values match */
  verifyInputsMatch(element: ElementRef, verifyElement: ElementRef): boolean {
    return element.nativeElement.value === verifyElement.nativeElement.value;
  }

  togglePaymentAcceptance(event: any) {
    this.paymentAccepted = event.checked;
  }

}
