import { BrowserModule } from '@angular/platform-browser';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { HttpRequestInterceptor } from './core/http/request-interceptors';

/* Angular Material */
import { MatInputModule,
  MatFormFieldModule,
  MatSelectModule,
  MatCheckboxModule,
  MatButtonModule,
  MatIconModule,
  MatGridListModule,
  MatToolbarModule,
  MatCardModule,
  MatStepperModule,
  MatDatepickerModule,
  MatRadioModule,
  MatListModule
} from '@angular/material';
import { MomentDateModule, MatMomentDateModule } from '@angular/material-moment-adapter';
/* Components */
import { AppComponent } from './app.component';
import { LandingComponent } from './components/landing/landing.component';
import { PrivacyComponent } from './components/privacy/privacy.component';
import { ApplicantInformationComponent } from './components/applicant-information/applicant-information.component';
import { StepperComponent } from './components/stepper/stepper.component';
import { SiteLocationComponent } from './components/site-location/site-location.component';
import { PaymentInformationComponent } from './components/payment-information/payment-information.component';
import { ConfirmEnrollmentComponent } from './components/confirm-enrollment/confirm-enrollment.component';

const routes: Routes = [
  { path: '*', component: LandingComponent },
  { path: 'applicant-information', component: ApplicantInformationComponent },
  { path: 'confirm-enrollment', component: ConfirmEnrollmentComponent },
  { path: 'payment-information', component: PaymentInformationComponent },
  { path: 'privacy', component: PrivacyComponent },
  { path: 'site-location', component: SiteLocationComponent },
  { path: '**', component: LandingComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    LandingComponent,
    PrivacyComponent,
    ApplicantInformationComponent,
    StepperComponent,
    SiteLocationComponent,
    PaymentInformationComponent,
    ConfirmEnrollmentComponent
  ],
  imports: [
    HttpModule,
    HttpClientModule,
    BrowserModule,
    BrowserAnimationsModule,
    ReactiveFormsModule,
    MatInputModule,
    MatFormFieldModule,
    MatSelectModule,
    MatCheckboxModule,
    MatButtonModule,
    MatIconModule,
    MatGridListModule,
    MatToolbarModule,
    MatCardModule,
    MatStepperModule,
    MatDatepickerModule,
    MatRadioModule,
    MatMomentDateModule,
    MomentDateModule,
    MatListModule,
    RouterModule.forRoot(routes)
  ],
  providers: [{
      provide: HTTP_INTERCEPTORS,
      useClass: HttpRequestInterceptor,
      multi: true
    }],
  bootstrap: [AppComponent]
})
export class AppModule { }
