import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DataService } from '../../services/data.service';
import { Observable, Subscription } from 'rxjs';
import { Agency, AgencyProfile } from '../../models';
import { StepperService } from '../stepper/stepper.service';

@Component({
  selector: 'app-privacy',
  templateUrl: './privacy.component.html',
  styleUrls: ['./privacy.component.css']
})
export class PrivacyComponent implements OnInit, OnDestroy {

  privacyActText: string;
  privacyAccepted: boolean;
  privacyForm: FormGroup;
  privacyText: string;
  selectedAgency: Agency;
  selectedAgencyProfile: AgencyProfile;
  stateAgencies: Agency[] = [];
  subscriptions: Subscription[] = [];
  constructor(public dataService: DataService, private stepperService: StepperService) {}

  ngOnInit() {
    this.setupForm();
    this.getPrivacyAct();
    this.populateFormFromStorage();
    this.getStates();
    this.getAgenciesByState('GA');
  }

  /* Remove subscriptions to clear memory */
  ngOnDestroy() {
    this.subscriptions.forEach(s=> s.unsubscribe());
  }

  getAgenciesByState(stateCode: string) {
    return this.dataService.getAgenciesByState(stateCode)
    .subscribe(response=> {
      console.log(response);
    });
  }

  getAgencyProfile() {
    console.log(this.privacyForm.controls['agency'].value)
    if(this.privacyForm.controls['agency'].value) {
      let agencyId = this.privacyForm.controls['agency'].value.id;
      this.selectedAgencyProfile = this.dataService.getAgencyProfile(agencyId);
      this.getAgencyPrivacy(this.selectedAgencyProfile.id);
    }
  }

  getAgencyPrivacy(agencyId: string) {
    this.subscriptions.push(this.dataService.getAgencyPrivacy(agencyId)
    .subscribe(privacy=> {
      this.privacyText = privacy.text();
    }));
  }

  getPrivacyAct() {
    this.subscriptions.push(this.dataService.getPrivacyAct()
    .subscribe(privacyAct=> {
      this.privacyActText = privacyAct.text();
    }));
  }

  getStateAgencies() {
    let state = this.privacyForm.controls['state'].value;
    this.stateAgencies = state ? this.dataService.getStateAgencies(state) : [];
  }

  getStates() {
    return this.dataService.getStates().
    subscribe(states=> {
      console.log(states)
    });
  }

  goToPreviousStep() {
    this.stepperService.goToPreviousStep();
  }

  next() {
    localStorage.setItem('privacy', JSON.stringify(this.privacyForm.value));
    console.log('call complete step')
    this.stepperService.completeStep();
  }

  populateFormFromStorage() {
    /* Auto populate saved privacy data? */
    // let privacy = localStorage.getItem('privacy');
    // if(privacy) {
    //   this.selectedAgency = JSON.parse(privacy).agency;
    //   this.privacyForm.patchValue(JSON.parse(privacy));
    //   this.privacyAccepted = true;
    // } else {
    //   this.privacyAccepted = false;
    // }
    this.privacyAccepted = false;
  }

  setupForm() {
    this.privacyForm = new FormGroup({
      state: new FormControl(null, Validators.required),
      agency: new FormControl(null, Validators.required)
    });
    this.subscriptions.push(this.privacyForm.controls['state'].valueChanges.subscribe(()=> this.getStateAgencies()));
    this.subscriptions.push(this.privacyForm.controls['agency'].valueChanges.subscribe(()=> this.getAgencyProfile()));
  }

  setSelectedAgency(agency: Agency) {
    this.selectedAgency = agency;
  }

  togglePrivacyAcceptance(event: any) {
    this.privacyAccepted = event.checked;
  }

}
