import { Component, AfterViewInit, OnInit, OnDestroy, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatHorizontalStepper } from '@angular/material';
import { StepperService, Step } from './stepper.service';
import { WindowService } from '../../services/window.service';
import { Observable, Subscription, timer } from 'rxjs';
import { Router } from '@angular/router';

@Component({
  selector: 'app-stepper',
  templateUrl: './stepper.component.html',
  styleUrls: ['./stepper.component.css']
})
export class StepperComponent implements OnInit, OnDestroy {

  @ViewChild('stepper') stepper: MatHorizontalStepper;

  clickedStepIndex: number;
  currentStep: any;
  currentStepIndex: number;
  currentStepSubscription: Subscription;
  initializedStepper: boolean = false;
  previousStepIndex: number;

  constructor(private router: Router, private stepperService: StepperService, public windowService: WindowService, private cdr: ChangeDetectorRef) {}

  ngOnInit() {
    this.currentStepIndex = 0;
    console.log(this.windowService)
  }

  ngOnDestroy() {
    this.currentStepSubscription.unsubscribe();
  }

  ngAfterViewInit() {
    this.stepperService.setActiveStep(0);
    this.currentStep = this.getStep(0);
    this.stepper.reset();
    this.cdr.detectChanges();
    this.currentStepSubscription = this.stepperService.currentStepSubscription$.subscribe(stepIndex=> this.changeStep(stepIndex));
  }

  changeStep(stepIndex: number) {
    if(stepIndex >= 0 && stepIndex < this.stepper._steps.length) {
      let previousStep = this.currentStep;
      this.currentStepIndex = stepIndex;
      this.stepper.selectedIndex = stepIndex;
      this.stepper._steps.forEach((step, index)=> {
        if(index === stepIndex && step != this.currentStep) {
          this.currentStep = step;
        }
      });
      if(previousStep != this.currentStep) {
        let step = this.stepperService.getCurrentStep();
        this.router.navigate([step.route]);
      }
    }
  }

  changeSteps(event: any) {
    console.log(event)
    if(this.stepperService.canCompleteStep(event.selectedIndex)) {
      this.stepperService.setActiveStep(event.selectedIndex);
      this.previousStepIndex = event.selectedIndex;
    } else if(this.previousStepIndex != event.selectedIndex) {
      timer(10).subscribe(()=> this.changeStep(this.currentStepIndex));
    }
  }

  getStep(stepIndex: number): any {
    return this.stepper._steps[stepIndex];
  }

}
