  /* Stub generating data - create an agency */
  generateAgency(state: string, idx: number, departments?: boolean): Agency {
    let agency = {
      id: this.generateAgencyId(state),
      name: this.generateAgencyName(state),
      state: state
    };
    agency['alias'] = agency.name+' - '+agency.id;
    agency['agencyProfileId'] = agency.id;
    this.generateAgencyProfile(agency.id);
    return agency;
  }

  /* Stub generating data - creates an agency id from state abbreviation */
  generateAgencyId(state: string): string {
    return state+Math.random().toString(36).substring(6).toUpperCase();
  }

  /* Stub generating data - create an agency profile */
  generateAgencyProfile(id: string) {
    let agencyProfile: AgencyProfile = {
      id: id,
      group: null,
      subGroup: null,
      reasons: this.generateReasons(),
      privacyLink: 'assets/files/privacy-1.txt',
      fee: this.generateEnrollmentType()
    };
    this.agencyProfiles.push(agencyProfile);
  }

  /* Stub generating data - create empty enrollment type */
  generateEnrollmentType(): EnrollmentType {
    return { type: null, electronicFee: null, paperCardFee: null };
  }

