import { Component, AfterViewInit, OnInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';
import { DataService } from './services/data.service';
import { StepperService } from './components/stepper/stepper.service';
import { WindowService } from './services/window.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  applicationAnchorTop: any;
  enrollmentHeadline: any;
  openMenu: boolean;
  showStepper: boolean;
  startEnrollment: boolean;

  constructor(private router: Router,
    private route: ActivatedRoute,
    private ds: DataService,
    private ss: StepperService,
    private ws: WindowService) {

    /*  Listen for route changes */
    this.router.events
    .subscribe((event) => {
      /* Only do something when navigation ends (navigation has multple events) */
      if (event instanceof NavigationEnd) {
        if(event.url === '/') {
          this.enrollmentHeadline = 'Welcome';
          this.startEnrollment = false;
        } else {
          this.enrollmentHeadline = 'Application Enrollment Management';
          this.startEnrollment = true;
        }
        this.ss.getActiveStepFromRoute(this.router.url);
        this.scrollAppAnchorIntoView();
      }
    });
  }

  ngOnInit() {
    this.openMenu = false;
  }

  /* Since Divs are fixed make sure the app div scrolls to top router navigate */
  scrollAppAnchorIntoView() {
    let anchor = document.querySelector('.naps-enrollment-top-anchor');
    if(anchor) anchor.scrollIntoView();
  }

  /* Show/Hide left menu */
  toggleMenu() {
    this.openMenu = !this.openMenu;
  }
}
